import { html, render } from 'lit-html';
import { of, Subject, throwError, merge, Observable } from 'rxjs';
import { fromFetch } from 'rxjs/fetch';
import { delay, filter, map, switchMap, startWith, catchError } from 'rxjs/operators';

const UserService = {
  login: (data: any) => of({ firstName: 'Ivan', lastName: 'Ivanov' }).pipe(delay(2000)),
  loadUsers: () => fromFetch('https://jsonplaceholder.typicode.com/users').pipe(switchMap(data => {
    if (data.ok) { return data.json(); }
    return throwError('Could not fetch!');
  }))
};


export class MessageBus {
  _mbus: Subject<{ type: string, data: any }>;

  constructor() {
    this._mbus = new Subject<{ type: string, data: any }>();
    this._mbus.subscribe(console.log);
  }

  listen(type: string) {
    return this._mbus.pipe(filter(m => m.type === type), map(m => m.data));
  }

  send(type: string, data: any) {
    this._mbus.next({ type, data });
  }

};

export class AppEffects {

  login$ = this.messageBus.listen('[AUTH] Login').pipe(
    switchMap(data => UserService.login(data).pipe(
      switchMap((user) => [
        { type: '[AUTH] Login Success', data: user },
        { type: '[USERS] Load Users', data: null }
      ]),
      startWith({ type: '[GLOBAL] Set Loader', data: true }),
      catchError(error => [{ type: '[USERS] Load Users Failed', data: error }])
    ))
  );

  loadUsers$ = this.messageBus.listen('[USERS] Load Users').pipe(
    switchMap(() => UserService.loadUsers().pipe(
      switchMap(users => [
        { type: '[USERS] Load Users Success', data: users },
        { type: '[GLOBAL] Set Loader', data: false }
      ]),
      catchError(error => [{ type: '[USERS] Load Users Failed', data: error }])
    ))
  );

  constructor(private messageBus: MessageBus) { }

  connect = () => {
    merge(...Object.values(this).filter(val => val instanceof Observable)).subscribe(this.messageBus._mbus);
  }
}


const appLogin = (context: App) => html`
<div>Login</div>
<form>
  <div class="form-group">
    <label for="email">Email</label>
    <input type="email" id="email" @keyup=${(e: any)=> context.formData.email = e.target.value}/>
  </div>
  <div class="form-group">
    <label for="password">Password</label>
    <input type="password" id="password" @keyup=${(e: any)=> context.formData.password = e.target.value}/>
  </div>
  <button type="button" @click=${() => context.loginHandler(context.formData)}>Login</button>
</form>
`;

const appLoading = (context: App) => html`<div>Loading...</div>`;

const appLoggedIn = (context: App) => html`<div>
  <div>Hello ${context.loggedUser.firstName}!</div>
  <div>${context.userList.map((user) => html`<li>${user.username}</li>`)}</div>
</div>`;

export class App extends HTMLElement {
  loggedUser: { firstName: string, lastName: string } = null;
  userList: any[] = [];
  isLoading = false;
  render: () => void;

  formData = {
    email: '',
    password: ''
  }

  constructor(private messageBus: MessageBus) {
    super();
    const root = this.attachShadow({ mode: 'closed' });
    this.render = () => {
      const template = this.isLoading ?
        appLoading : !this.loggedUser ? appLogin : appLoggedIn;
      render(template(this), root);
    }

    this.render();

    messageBus.listen('[AUTH] Login Success').subscribe(user => {
      this.loggedUser = user;
      this.render();
    });

    messageBus.listen('[USERS] Load Users Success').subscribe(users => {
      this.userList = users;
      this.render();
    });

    messageBus.listen('[GLOBAL] Set Loader').subscribe(isLoading => {
      this.isLoading = isLoading;
      this.render();
    });
  }

  loginHandler = (formData: { email: string, password: string }) => {
    this.messageBus.send('[AUTH] Login', formData);
  }
}

customElements.define('hg-app', App);